\r\n\r\n\r\n:focus {\r\n\tcolor: #333;\r\n\toutline: #ccc solid 1px;\r\n}\r\n\r\n\r\n/* Site Header\r\n---------------------------------------------------------------------------------------------------- */\r\n\r\n\r\n.site-header {\r\n\tbackground-color: #fff;\r\n\tbox-shadow: 0 0 20px rgba(0,0,0,0.05);\r\n\tpadding: 0 30px;\r\n}\r\n\r\n\r\n/* Title Area\r\n--------------------------------------------- */\r\n\r\n\r\n.title-area {\r\n\tfloat: left;\r\n\tpadding-bottom: 25px;\r\n\tpadding-top: 25px;\r\n}\r\n\r\n\r\n.wp-custom-logo .title-area {\r\n\tmax-width: 350px;\r\n\tpadding-bottom: 5px;\r\n\tpadding-top: 5px;\r\n}\r\n\r\n\r\n.site-title {\r\n\tfont-size: 20px;\r\n\tfont-size: 2rem;\r\n\tfont-weight: 600;\r\n\tline-height: 1;\r\n\tmargin-bottom: 0;\r\n}\r\n\r\n\r\n.site-title a,\r\n.site-title a:focus,\r\n.site-title a:hover {\r\n\tcolor: #333;\r\n\ttext-decoration: none;\r\n}\r\n\r\n\r\n.site-description,\r\n.wp-custom-logo .site-title {\r\n\tborder: 0;\r\n\tclip: rect(0, 0, 0, 0);\r\n\theight: 1px;\r\n\toverflow: hidden;\r\n\tposition: absolute !important;\r\n\twidth: 1px;\r\n\tword-wrap: normal !important;\r\n}\r\n\r\n\r\n/* Site Navigation\r\n---------------------------------------------------------------------------------------------------- */\r\n\r\n\r\n.genesis-nav-menu {\r\n\tclear: both;\r\n\tline-height: 1;\r\n\twidth: 100%;\r\n}\r\n\r\n\r\n.genesis-nav-menu .menu-item {\r\n\tdisplay: block;\r\n\tfloat: none;\r\n\tposition: relative;\r\n}\r\n\r\n\r\n.genesis-nav-menu a {\r\n\tcolor: #333;\r\n\tdisplay: block;\r\n\tfont-size: 15px;\r\n\tfont-size: 1.5rem;\r\n\tfont-weight: 400;\r\n\toutline-offset: -1px;\r\n\tpadding-bottom: 12px;\r\n\tpadding-top: 12px;\r\n\ttext-decoration: none;\r\n}\r\n\r\n\r\n.genesis-nav-menu a:focus,\r\n.genesis-nav-menu a:hover,\r\n.genesis-nav-menu .current-menu-item > a,\r\n.genesis-nav-menu .sub-menu .current-menu-item > a:focus,\r\n.genesis-nav-menu .sub-menu .current-menu-item > a:hover {\r\n\tcolor: #0073e5;\r\n\ttext-deco